"use strict";
import { ChartHeader } from "components/Charts/ChartHeader";
import { Chart, ChartModel } from "components/Charts/ChartModel";
import { StackedAreaSeries } from "components/Charts/StackedLineChart/stacked-area-series/stacked-area-series";
import { getProtocolColor } from "graphql/data/util";
import {
  LineStyle
} from "lightweight-charts";
import { useMemo } from "react";
import { useSporeColors } from "ui/src";
class TVLChartModel extends ChartModel {
  series;
  hoveredLogicalIndex;
  constructor(chartDiv, params) {
    super(chartDiv, params);
    this.series = this.api.addCustomSeries(new StackedAreaSeries(), {});
    this.series.setData(this.data);
    this.updateOptions(params);
    this.fitContent();
    this.api.subscribeCrosshairMove((param) => {
      if (param?.logical !== this.hoveredLogicalIndex) {
        this.hoveredLogicalIndex = param?.logical;
        this.series.applyOptions({
          hoveredLogicalIndex: this.hoveredLogicalIndex ?? -1
          // -1 is used because series will use prev value if undefined is passed
        });
      }
    });
  }
  updateOptions(params) {
    const isSingleLineChart = params.colors.length === 1;
    const gridSettings = isSingleLineChart ? {
      grid: {
        vertLines: { style: LineStyle.CustomDotGrid, color: params.theme.neutral3 },
        horzLines: { style: LineStyle.CustomDotGrid, color: params.theme.neutral3 }
      }
    } : {};
    super.updateOptions(params, {
      handleScale: false,
      handleScroll: false,
      rightPriceScale: {
        visible: isSingleLineChart,
        // Hide pricescale on multi-line charts
        borderVisible: false,
        scaleMargins: {
          top: 0.25,
          bottom: 0
        },
        autoScale: true
      },
      ...gridSettings
    });
    const { data, colors, gradients } = params;
    if (this.data !== data) {
      this.data = data;
      this.series.setData(data);
      this.fitContent();
    }
    this.series.applyOptions({
      priceLineVisible: false,
      lastValueVisible: false,
      colors,
      gradients,
      lineWidth: 2.5
    });
  }
}
export function LineChart({ height, data, sources, stale }) {
  const sporeColors = useSporeColors();
  const params = useMemo(() => {
    const colors = sources?.map((source) => getProtocolColor(source)) ?? [sporeColors.accent1.val];
    return { data, colors, stale };
  }, [data, sporeColors, sources, stale]);
  const lastEntry = data[data.length - 1];
  return <Chart Model={TVLChartModel} params={params} height={height}>{(crosshairData) => <ChartHeader
    value={(crosshairData ?? lastEntry)?.values.reduce((v, sum) => sum += v, 0)}
    time={crosshairData?.time}
    protocolData={sources?.map((source, index) => ({ protocol: source, value: crosshairData?.values[index] }))}
  />}</Chart>;
}
